
import React from "react";
import axios from "axios";
import { blogURI } from "../../../globals"


class Form extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mailSent: false,
      error: null,

    };
  }

  handleFormSubmit = e => {
    e.preventDefault();
    
    var bodyFormData = new FormData();
    bodyFormData.append('formid', '1' ); 
    bodyFormData.append('url', this.state.url ); 
    bodyFormData.append('title', this.state.title ); 
    bodyFormData.append('name', this.state.name ); 
    bodyFormData.append('phone', this.state.phone ); 
    bodyFormData.append('email', this.state.email ); 
    bodyFormData.append('notes', this.state.notes ); 
    axios({
      method: "post",
      url: 'https://forms.alphaland.com.vn/form_submit.php',
      headers: { "Content-Type": "application/json" },
      data: bodyFormData
    })
      .then(result => {
        if (result.data.sent) {
          this.setState({
            mailSent: result.data.sent
          });
          this.setState({ error: false });
        } else {
          this.setState({ error: true });
        }
      })
      .catch(error => this.setState({ error: error.message }));
  };

  //handle changing fields

  handleChange = (e, field) => {
    let value = e.target.value;
    let updateValue = {};
    updateValue[field] = value;
    this.setState(updateValue);
  };

  render() {
    const { post } = this.props.pageContext;
    const note = 'Xin cho Alphaland! Ti ang quan tm dn d n ' + post.title;
    const linkto = 'https://www.sharkhung.vn/' + blogURI + '/' + post.uri;
    this.state.url = linkto;
    this.state.title = post.title;
    return (
      <div className="form-wrapper">
        <h3>Lin h t vn</h3>
        <form action="#">
            <div className="form-group">
              <input
                type="hidden"
                className="form-control"
                value="1"
                onChange={e => this.handleChange(e, 'formid')}
              />
            </div>
            <div className="form-group">
              <input
                type="hidden"
                className="form-control"
                value={this.state.title}
                onChange={e => this.handleChange(e, 'url')}
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                type="text"
                placeholder="H v tn *"
                value={this.state.name}
                onChange={e => this.handleChange(e, 'name')}
                required
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                type="text"
                placeholder="S in thoi *"
                value={this.state.phone}
                onChange={e => this.handleChange(e, 'phone')}
                required
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                type="text"
                placeholder="a ch email"
                value={this.state.email}
                onChange={e => this.handleChange(e, 'email')}
              />
            </div>
            <div className="form-group">
              <input
                type="text"
                className="form-control"
                value={this.state.title}
                disabled
                onChange={e => this.handleChange(e, 'title')}
              />
            </div>
            <div className="form-group">
              <textarea
                className="form-control"
                placeholder={note}
                value={this.state.notes}
                onChange={e => this.handleChange(e, 'notes')}
                rows="5"
              />
            </div>

            <input type="submit" onClick={e => this.handleFormSubmit(e)} value="Gi" />

            <div>
              {this.state.mailSent && <div className="alert alert-success">Cm n qu khch, chng ti s lin h vi qu khch ngay khi c th</div>}
              {this.state.error && <div className="alert alert-danger"> c li xy ra</div>}
            </div>
          </form>
      </div>
    );
  }
}

export default Form;